@font-face {
  font-family: 'MontserratRegular';
  src: url('./assets/fonts/MONTSERRAT-REGULAR.TTF') format('truetype');
}
@font-face {
  font-family: 'MontserratBold';
  src: url('./assets/fonts/MONTSERRAT-BOLD.TTF') format('truetype');
}
@font-face {
  font-family: 'MontserratMedium';
  src: url('./assets/fonts/MONTSERRAT-MEDIUM.TTF') format('truetype');
}
@font-face {
  font-family: 'MontserratSemibold';
  src: url('./assets/fonts/MONTSERRAT-SEMIBOLD.TTF') format('truetype');
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


h1{
  font-family: MontserratBold;
  font-size: 32px;
  font-weight: bold;
  color: #262626;
  margin-bottom: 0;
}
form{
  display: flex;
  gap: 16px;
  padding: 20px 0;
  flex-direction: column;
}
label{
  font-family: MontserratSemibold;
  color: #262626;
  display: block;
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 4px;
}
input, select {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  font-size: 14px;
  background-color: #F4F4F4;
  border: 2px solid #9C9C9C;
  border-radius: 8px;
}
.form-2{
  display: flex;
}
.form-2 > *{
  width: 30%;
  margin: 0 30px;
}
.descrição{
  font-family: MontserratRegular;
  font-size: 14px;
  color: #262626;
  margin-top: 0;
  padding-top: 8px;
}
.onde-encontrar {
  font-family: MontserratBold;
  letter-spacing: 1px;
  display: block;
  margin: 10px 0 20px;
  font-size: 12px;
  text-align: right;
  margin: 0;
  color: #555;
  text-decoration: underline;
  cursor: pointer;
}
.btn-solicitar {
  font-family: MontserratBold;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  background-color: #63141B; /* Cor do botão */
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 2px;
}
footer{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}
footer img{
  height: 30px;
  padding: 0 10px;
  filter: grayscale(100%);
  filter: gray;
}
.line-left, .line-right {
  flex-grow: 1;
  height: 2px;
  background-color: #aaa; /* Cor das linhas */
  border: none;
}

.d-flex{
  display: flex;
  background-color: #F6F6F6;
}
.w-50{
  width: 50%;
  height: 100vh;
  display: flex;
  align-items: center;
}
.va-middle{
  box-sizing: border-box;
  margin: 0 auto; /* Centraliza horizontalmente */
  padding: 20px;
  max-width: 50%;
  text-align: left;
  font-family: Arial, sans-serif;

}
/* Input Page */
.input {
  margin: 40px;
}
.form{
  margin: 16px 16px;
}
label {
  margin-right: 10px;
}

input, select {
  margin-bottom: 10px;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
}

table, th, td {
  border: 1px solid black;
}

th, td {
  padding: 10px;
  text-align: left;
}


/* Modal overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal container */
.modal-container {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  width: 90%;
  max-width: 400px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.modal-success {
  border-top: 5px solid #4caf50;
}

.modal-error {
  border-top: 5px solid #8D021A;
}

.modal-loading {  border-top: 5px solid #ffffff;
}

/* Ícones de sucesso/erro */
.modal-icon {
  font-size: 48px;
  margin-bottom: 10px;
}

.modal-check {
  color: #4caf50;
}

.modal-warning {
  color: #8D021A;
}

/* Modal título e mensagem */
.modal-title {
  font-family: MontserratBold;
  font-size: 20px;
  margin-bottom: 10px;
  color: #262626;
}

.modal-message {
  font-family: MontserratRegular;
  font-size: 16px;
  margin-bottom: 20px;
  color: #262626;
}

/* Botão do modal */
.modal-button {
  background: #4caf50;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  width: 100%;
  font-family: MontserratBold;
}

.modal-button:hover {
  background: #45a049;
}

.modal-error .modal-button {
  background: #8D021A;
}

.modal-error .modal-button:hover {
  background: #970c23;
}

/* Spinner de carregamento */
.modal-loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-top: 4px solid #2196f3;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin-bottom: 10px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


.img-context{
  border-radius: 24px;
  box-sizing: border-box;
  padding: 16px 32px;
  height: 100%;
  width: 100%;
  background-image: url('./assets/images/img.jpeg');
  background-size: cover;
  background-position: center; /* Centraliza a imagem */
  background-repeat: no-repeat; /* Evita repetições */
}
.parent{
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  padding: 30px;
}

@keyframes App-logo-spin {
  
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


::-webkit-input-placeholder {
  color: #26262640;
}

:-moz-placeholder { /* Firefox 18- */
  color: #26262640;  
}

::-moz-placeholder {  /* Firefox 19+ */
  color: #26262640;  
}

:-ms-input-placeholder {  
  color: #26262640;  
}


@media (max-width: 1024px) {
  .va-middle {
    max-width: 90%;
  }
}
@media (max-width: 592px) {
  .d-flex {
    flex-direction: column-reverse;
  }
  .w-100{
    width: 100%;
    height: 100%;
  }
  .h-50-480{
    height: 30vh !important; 
    margin-top: 24px;
  }
  .parent{
    margin: 0 auto;
    max-width: 90%;
    padding: 0;
  }
  .va-middle{
    padding: 0;
  }
  
  h1{
    font-size: 24px;
  }
  h2{
    font-size: 14px;
  }
}



.modal {
  font-size: 12px;
}
.modal > .header {
  width: 100%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 100%;
  padding: 10px 5px;
}
.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}
